import uniRate from '@/components/uni-rate/uni-rate.vue'
export default {
	name: "",
	components: {
		uniRate
	},
	props: {},
	data() {
		return {
			uLikeList: []
		}
	},
	methods: {
		// 跳转到详情页
		goDetails(id) {
			uni.navigateTo({
				url: `../details/details?id=${id}`
			});
		},
		// 获取猜你喜欢数据
		getULike() {
			uni.showLoading()
			uni.request({
				url: "https://www.imovietrailer.com/superhero/index/guessULike?qq=2684425594",
				method: 'POST',
				data: {
					qq: 2684425594,
					type: 'superhero'
				},
				header: {
					'custom-header': 'application/json' //自定义请求头信息
				},
				success: res => {

					if (res.statusCode === 200) {
						this.uLikeList = res.data.data
						console.log(this.uLikeList)
					}
				},
				fail: () => {},
				complete: () => {
					uni.hideLoading()
				}
			})
		}
	},
	beforeMount() {
		this.getULike()
	},
	mounted() {

	},
	onLoad() {

	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
